import Vue from "vue";
import App from "./App.vue";
import router from "./router";
import "@/assets/css/normalize-and-reset.css";
import "@/assets/css/global.css";
import store from "./store";
import { CART_STORAGE_KEY } from "./store";
import Vuelidate from "vuelidate";
import TreeView from "vue-json-tree-view";

Vue.config.productionTip = false;

Vue.use(Vuelidate);
Vue.use(TreeView);
const formatter = new Intl.NumberFormat("en-US", {
  style: "currency",
  currency: "USD",
  minimumFractionDigits: 2
});

Vue.filter("currency", function(value) {
  if (typeof value !== "number") {
    return value;
  }

  return formatter.format(value / 100);
});

new Vue({
  router,
  store, // every component will reference the Vuex.Store now
  render: function(h) {
    return h(App);
  },
  created() {
    const cartString = localStorage.getItem(CART_STORAGE_KEY);
    if (cartString) {
      const shoppingCart = JSON.parse(cartString);
      this.$store.commit("SET_CART", shoppingCart);
    }
  }
}).$mount("#app");
