<template>
  <div class="rightCategoryItem">
    <img
      id="bookImage"
      :src="require('../assets/images/books/' + bookImageFileName(book))"
      :alt="book.title"
      style="width:130px;height:200px;"
    />
    <div id="bookInfo">
      <h2>{{ book.title }}</h2>
      <h3>by {{ book.author }}</h3>
      <h4>{{ book.price | currency }}</h4>
      <ul id="bookButtons">
        <li>
          <a href="#" id="addToCartButton" v-on:click="addToCart(book)"
            >Add to Cart
          </a>
        </li>
        <li v-if="book.public" id="readNowButton"><a href="#">Read</a></li>
      </ul>
    </div>
  </div>
</template>

<script>
export default {
  name: "categoryBookListItem",
  props: {
    book: {
      type: Object,
      required: true
    }
  },
  methods: {
    bookImageFileName: function(book) {
      let name = book.title.toLowerCase();
      name = name.replace(/ |:/g, "");

      return `${name}.gif`;
    },
    addToCart: function(book) {
      this.$store.dispatch("addToCart", book);
    }
  }
};
</script>
