<template>
  <div id="genrePage">
    <category-nav></category-nav>
    <category-book-list> </category-book-list>
  </div>
</template>

<script>
import CategoryNav from "@/components/CategoryNav";
import CategoryBookList from "@/components/CategoryBookList";

export default {
  name: "category",
  components: {
    CategoryNav,
    CategoryBookList
  },
  mounted: function() {
    this.$store.dispatch("selectCategory", this.$route.params.name);
    this.$store.dispatch("fetchSelectedCategoryBooks").catch(reason => {
      console.log("Category.vue dispatching 404 due to reason: " + reason);
      this.$store.dispatch("selectCategory", "");
      this.$router.push({ name: "404" });
    });
  }
};
</script>

<style scoped>
#genrePage {
  display: flex;
  flex-direction: row;
  background: #5b9bd5;
}
</style>
