<template>
  <div id="cartPage">
    <div id="cartButtons">
      <router-link
        :to="{
          name: 'category',
          params: { name: $store.state.selectedCategoryName }
        }"
        tag="button"
        >Continue Shopping</router-link
      >
      <router-link
        v-if="$store.state.cart.numberOfItems"
        :to="'checkout'"
        tag="button"
        id="proceedButton"
        >Proceed to Checkout</router-link
      >
      <button
        id="emptyButton"
        v-if="$store.state.cart.numberOfItems"
        @click="empty()"
      >
        Empty Cart
      </button>
    </div>
    <h1 v-if="$store.state.cart.numberOfItems">
      {{ $store.state.cart.numberOfItems }} Item(s) in cart
    </h1>
    <h1 v-else>Your cart is empty.</h1>
    <cart-table> </cart-table>
  </div>
</template>

<script>
import CartTable from "@/components/CartTable";

export default {
  name: "Cart",
  components: {
    CartTable
  },
  methods: {
    empty() {
      this.$store.dispatch("emptyCart", this.$store.state.cart);
    }
  }
};
</script>

<style scoped>
#cartPage {
  text-align: center;
  background-color: #5b9bd5;
  padding-bottom: 10px;
  padding-top: 10px;
  display: flex;
  flex-direction: column;
}

h1 {
  margin: 10px;
}
#cartButtons {
  display: flex;
  justify-content: center;
  padding: 5px;
  flex-wrap: wrap;
}

button {
  background-color: #f4b183;
  margin: 10px;
  padding: 5px;
  cursor: pointer;
}
#subtotalCSS {
  font-size: 45px;
  font-weight: bolder;
}

#proceedButton {
  background-color: green;
  color: white;
}

#emptyButton {
  background-color: red;
  color: white;
}
</style>
