<template>
  <div id="checkoutPage">
    <p>Checkout</p>
    <br />
    <section class="checkoutInfo" v-if="!cart.empty">
      <form @submit.prevent="submitOrder">
        <div>
          <label for="name">Name</label>
          <div class="inputCSS">
            <input
              type="text"
              size="20"
              id="name"
              name="name"
              v-model.lazy="$v.name.$model"
            />
          </div>
        </div>
        <template v-if="$v.name.$error">
          <span class="error" v-if="!$v.name.required">Name is required</span>
          <span class="error" v-else-if="!$v.name.minLength">
            Name must have at least
            {{ $v.name.$params.minLength.min }} letters.
          </span>
          <span class="error" v-else-if="!$v.name.maxLength">
            Name can have at most
            {{ $v.name.$params.maxLength.max }} letters.
          </span>
        </template>

        <div>
          <label for="address">Address</label>
          <div class="inputCSS">
            <input
              type="text"
              size="20"
              id="address"
              name="address"
              v-model.lazy="$v.address.$model"
            />
          </div>
        </div>
        <template v-if="$v.address.$error">
          <span class="error" v-if="!$v.address.required"
            >Address is required</span
          >
          <span class="error" v-if-else="!$v.address.minLength">
            Address must have at least
            {{ $v.address.$params.minLength.min }} letters.
          </span>
          <span class="error" v-if-else="!$v.address.maxLength">
            Address can have at most
            {{ $v.address.$params.maxLength.max }} letters.
          </span>
        </template>
        <div>
          <label for="phone">Phone</label>
          <div class="inputCSS">
            <input
              class="textField"
              type="tel"
              size="20"
              id="phone"
              name="phone"
              v-model.lazy="$v.phone.$model"
            />
          </div>
        </div>
        <template v-if="$v.phone.$error">
          <span class="error" v-if="!$v.phone.required">Phone is required</span>
          <span class="error" v-else-if="!$v.phone.phone">
            Not a valid phone number.
          </span>
        </template>
        <div>
          <label for="email">Email</label>
          <div class="inputCSS">
            <input
              type="email"
              size="20"
              id="email"
              name="email"
              v-model.lazy="$v.email.$model"
            />
          </div>
        </div>

        <template v-if="$v.email.$error">
          <span class="error" v-if="!$v.email.required">Email is required</span>
          <span class="error" v-else-if="!$v.email.email">
            Not a valid email.
          </span>
        </template>
        <div>
          <label for="ccNumber">Credit card</label>
          <div class="inputCSS">
            <input
              type="text"
              size="20"
              id="ccNumber"
              name="ccNumber"
              v-model.lazy="$v.ccNumber.$model"
            />
          </div>
        </div>
        <template v-if="$v.ccNumber.$error">
          <span class="error" v-if="!$v.ccNumber.required"
            >Credit card number is required</span
          >
          <span class="error" v-else-if="!$v.ccNumber.email">
            Not a valid credit card number.
          </span>
        </template>
        <table>
          <tr>
            <th for="ccExpiryMonth">Exp Month</th>
            <th for="ccExpiryYear">Exp Year</th>
          </tr>
          <tr>
            <td>
              <select v-model="ccExpiryMonth">
                <option
                  v-for="(month, index) in months"
                  :key="index"
                  :value="index + 1"
                  >{{ month }} ({{ index + 1 }})</option
                >
              </select>
            </td>
            <td>
              <select v-model="ccExpiryYear">
                <option
                  v-for="(year, index) in 15"
                  :key="yearFrom(index)"
                  :value="yearFrom(index)"
                  >{{ yearFrom(index) }}</option
                >
              </select>
            </td>
          </tr>
        </table>

        <table id="totalsTable">
          <tr>
            <td>Subtotal:</td>
            <td id="subTotal" class="totalsAlign">
              {{ $store.state.cart.subtotal | currency }}
            </td>
          </tr>
          <tr>
            <td>Surcharge:</td>
            <td id="surcharge" class="totalsAlign">
              {{ $store.state.cart.surcharge | currency }}
            </td>
          </tr>
          <tr>
            <td>Total:</td>
            <td id="total" class="totalsAlign">
              {{ $store.state.cart.total | currency }}
            </td>
          </tr>
        </table>

        <input
          id="completePurchaseButton"
          type="submit"
          name="submit"
          :disabled="checkoutStatus == 'PENDING'"
          value="Complete Purchase"
        />
      </form>
      <section v-if="checkoutStatus != ''" class="checkoutStatusTextHolder">
        <template v-if="checkoutStatus == 'ERROR'">
          <div class="checkoutStatusText checkoutStatusErrorText">
            Please fix the problems above and try again.
          </div>
        </template>

        <template v-if="checkoutStatus == 'PENDING'">
          <div class="checkoutStatusText checkoutStatusPendingText">
            Processing...
          </div>
        </template>
        <template v-if="checkoutStatus == 'OK'">
          <div class="checkoutStatusText checkoutStatusOKText">
            Order placed...
          </div>
        </template>
        <template v-if="checkoutStatus == 'SERVER_ERROR'">
          <div class="checkoutStatusText checkoutStatusErrorText">
            An unexpected error occurred, please try again.
          </div>
        </template>
      </section>
    </section>
    <section class="temporaryStyle" v-else>
      <h1>You must have an item in your cart to checkout</h1>
      <router-link
        :to="{
          name: 'category',
          params: { name: $store.state.selectedCategoryName }
        }"
        tag="button"
        >Continue Shopping</router-link
      >
    </section>
  </div>
</template>

<script>
import {
  required,
  // eslint-disable-next-line no-unused-vars
  email,
  minLength,
  maxLength
} from "vuelidate/lib/validators";

import isCreditCard from "validator/lib/isCreditCard";
import isMobilePhone from "validator/lib/isMobilePhone";

// eslint-disable-next-line no-unused-vars
const phone = value => isMobilePhone(value, "en-US");
// eslint-disable-next-line no-unused-vars
const creditCard = value => isCreditCard(value);

export default {
  name: "Checkout",
  data() {
    return {
      name: "",
      address: "",
      phone: "",
      email: "",
      ccNumber: "",
      ccExpiryMonth: new Date().getMonth() + 1,
      ccExpiryYear: new Date().getFullYear(),
      checkoutStatus: ""
    };
  },
  validations: {
    name: {
      required,
      minLength: minLength(4),
      maxLength: maxLength(45)
    },
    address: {
      required,
      minLength: minLength(4),
      maxLength: maxLength(45)
    },
    email: {
      required,
      email
    },
    phone: {
      required,
      phone
    },
    ccNumber: {
      required,
      creditCard
    }
  },

  computed: {
    cart() {
      return this.$store.state.cart;
    },
    months() {
      return [
        "January",
        "February",
        "March",
        "April",
        "May",
        "June",
        "July",
        "August",
        "September",
        "October",
        "November",
        "December"
      ];
    }
  },

  methods: {
    submitOrder() {
      console.log("Submit order");
      this.$v.$touch(); // Ensures validators always run
      if (this.$v.$invalid) {
        this.checkoutStatus = "ERROR";
      } else {
        this.checkoutStatus = "PENDING";
        this.$store
          .dispatch("placeOrder", {
            name: this.name,
            address: this.address,
            phone: this.phone,
            email: this.email,
            ccNumber: this.ccNumber,
            ccExpiryMonth: this.ccExpiryMonth,
            ccExpiryYear: this.ccExpiryYear
          })
          .then(() => {
            this.checkoutStatus = "OK";
            this.$router.push({ name: "confirmation" });
          })
          .catch(reason => {
            this.checkoutStatus = "SERVER_ERROR";
            console.log("Error placing order", reason);
          });
      }
    },

    /* NOTE: For example yearFrom(0) == 2019 */
    yearFrom(index) {
      return new Date().getFullYear() + index;
    }
  }
};
</script>

